:root {
  --body-background: #e3ecff;
  --calculator-background: #3a4452;
  --shadow-1: #ffffff1a;
  --shadow-2: #0003;
  --text--color: #fff;
  --scrollBAr-color: grey;
  --cal-operator-color: #33ffd8;
  --cal-function-color: #ff333d;
  --active--color: grey;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

.container {
  background: var(--body-background);
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
}

.calculator {
  background: #3a4452;
  border-radius: 10px;
  padding: 20px;
}

.btn-container {
  grid-template-columns: repeat(5, auto);
  column-gap: 5px;
  display: grid;
}

.calculator form button, textarea {
  box-shadow: -4px -4px 10px var(--shadow-1), 4px 4px 10px var(--shadow-2);
  cursor: pointer;
  background: none;
  border: 0;
  border-radius: 10px;
  outline: 0;
  width: 60px;
  height: 60px;
  font-size: 20px;
}

form .display {
  justify-content: flex-end;
  margin: 10px 0;
  display: flex;
}

form .display textarea {
  text-align: justify;
  box-shadow: none;
  resize: none;
  vertical-align: bottom;
  scroll-behavior: smooth;
  flex: 1;
  height: 145px;
  padding: 0 10px;
}

form .display textarea::-webkit-scrollbar {
  width: 10px;
}

form .display textarea::-webkit-scrollbar-thumb {
  background-color: var(--scrollBAr-color);
  border-radius: 5px;
}

.calculator form button:not(.operator, .function), textarea {
  color: var(--text--color);
}

.operator {
  color: var(--cal-operator-color);
}

.function {
  color: var(--cal-function-color);
}

.active {
  background: var(--active--color) !important;
}
